.questionList {
    padding-right: 50%;
    background-color: #A9AF57 ;

}

.answerButtons{
    flex-direction: column;
    align-items: center;
    font-family: 'Trebuchet', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: larger;
    padding-left:2%;
    padding-right:2%;
    padding-top: 2%;
}


header{
    display:flexbox;
}

Button {
    
}


.simple_question{
    background-color: #888d45 ;
    text-align: 50%;
    margin-top: 1%;
    margin-bottom: 10%;
    padding:50px;
    margin-left: 15%;
    margin-right: 15%;
    border-radius: 15px;
    font-family: Arial, Helvetica, sans-serif;
    
}

.next-btn, .next-btn:disabled{
    margin:1%;
    border-radius: 10%;
    background:#4c755b;
    border-color:transparent;
    box-shadow: none;
}

.next-btn:hover {
    margin:1%;
    border-radius: 10%;
    background:#35513f;;
    border-color:transparent;
    box-shadow: none;
}

.submit-btn, .submit-btn:disabled{
    margin:1%;
    border-radius: 10%;
    background:#4c755b;
    border-color:transparent;
    box-shadow: none;
}

.submit-btn:hover{
    margin:1%;
    border-radius: 10%;
    background:#35513f;;
    border-color:transparent;
    box-shadow: none;
}

.question{
    font-family: 'Trebuchet', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size:1.5rem;
    font-weight: bold;
    color:#333;
    border-radius: 8px;
    border: 2px solid #ddd;
    background-color:#f9f9f9;
    padding:1rem;
    box-shadow:2px 4px 8px rgba(0,0,0,0.1);
    
}

.overlay{
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    background-color:rgba(0,0,0,0.5);
}